.options {
  width: 70%;
  margin: 40px auto;
  font-family: $b-fontFamily-heading, cursive, sans-serif;

  th.options-type {
    text-align: center;
    background-color: MDcolor(grey, 300);
  }

  th, td {
    padding: 10px 15px;
  }

  tr {
    border-bottom: 1px MDcolor(grey, 300) solid;
  }

  th {
    text-align: left;
    background-color: MDcolor(grey, 400);
    &:last-child {
      text-align: center;
    }
  }

  code {
    background-color: MDcolor(grey, 300);
    border-radius: 3px;
    padding: 2px 5px;
  }
}



