.popupbox-trigger {
  position: absolute;
  transition: all .2s;
  color: #fff;
  text-shadow: 0 -1px 1px #000;
  background-color: MDcolor(blue-grey, 900);
  box-shadow: 0 1px 1px rgba(black, .4);
  font-family: $b-fontFamily-heading, cursive, sans-serif;
  font-size: 24px;
  padding: 12px 16px 16px;
  height: 20px;
  width: 200px;
  top: 50%;
  left: 50%;
  margin: -10px 0 0 -116px;
  text-align: center;
  border-radius: 5px;
  cursor: pointer;
  display: block;

  &:hover {
    box-shadow: 2px 3px 5px rgba(black, .2);
    margin-top: -12px;
  }
}

.quotes-from {
  color: MDcolor(grey, 500);
  font-weight: bold;
  float: right;
}

.quotes {
  color: MDcolor(grey, 800);
  font-style: italic;
  line-height: 1em;
}

.demo-component {
  position: relative;
  text-align: center;
  min-height: 100px;
}

.demo-source {
  overflow-x: auto;
}

.demo-source pre {
  margin: auto;
  border: none;
  width: max-content;
  min-width: 80%;
}

.demo-source code {
  padding-right: 2em;
}

.demo-componentDescribe {
  display: block;
  text-align: center;
}

h2 {
  text-align: center;
  margin-top: 40px;
  font-size: 26px;
  color: MDcolor(grey, 700);
}

.lightbox-imgBtns {
  list-style: none;
}

.lightbox-imgBtns li {
  display: inline-block;
  width: 200px;
  height: 150px;
  margin: 0 5px;
  background-size: cover;
  background-position: center;
}

.lightbox-imgBtns li:first-child { background-image: url('http://fraina.github.io/react-imagebox/static/THE_PROPERTY_OF_JENS_BERGE.jpg'); }
.lightbox-imgBtns li:nth-child(2) { background-image: url('http://fraina.github.io/react-imagebox/static/beatles.jpg'); }
.lightbox-imgBtns li:last-child { background-image: url('http://fraina.github.io/react-imagebox/static/DUBLIN_BUT_WHERE_MAIN_STREET_IN_BLACKROCK.jpg'); }

.demo-third {
  color: #fff;
  text-align: left;
  font-family: 'Satisfy', $b-fontFamily-heading, 'Arial', 'sens-serif';
  overflow: hidden;
}

.demo-third p {
  margin: 0;
  font-size: 1.2em;
  line-height: 1.5;
  float: right;
  margin: 60px 30px 0 230px;
}

.demo-grainOfSand {
  background: url('http://fraina.github.io/react-imagebox/static/william-blake.jpg') no-repeat;
  background-color: #000;
  height: 300px;
  width: 600px;
}

.demo-grainOfSand p {
  width: 350px;
}

.demo-clothsOfHeaven p {
  width: 380px;
}

.demo-clothsOfHeaven {
  background: url('http://fraina.github.io/react-imagebox/static/William_Butler_Yeats.jpg') no-repeat;
  background-color: #1d1d1d;
  height: 395px;
  width: 750px;
}

.demo-grainOfSand--name,
.demo-clothsOfHeaven--name{
  display: block;
  text-align: right;
  margin-top: 30px;
}

.demo-somethingElse {
  width: 70%;
  display: block;
  margin: auto;
  background-color: MDcolor(grey, 300);
  padding: 1em;
  margin-top: 1em;
  h2 {
    margin-top: 0;
  }
}



